import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';

import { BridgeService } from './bridge.service'
import { SidebarGeneralListComponent } from '../../_sidebars/sidebar-general-list/sidebar-general-list.component'

import { Subject, BehaviorSubject } from 'rxjs';
import { tap, takeUntil } from 'rxjs/operators';

@Component({
  selector: 'kt-field-general-list',
  templateUrl: './field-general-list.component.html',
  styleUrls: ['./field-general-list.component.scss']
})
export class FieldGeneralListComponent implements OnInit {

  @Output() returnData = new EventEmitter<any>();
  private _data1 = new BehaviorSubject<any[]>([])
  //private _data2 = [] //used for preview
  private destroy$ = new Subject();
  @Input() params
  @Input() errors
  // @Input() set preload(preload: any) {
  //   //this._data2 = preload
  // }

  @Input() set data(data: any) {
    // console.log(data)
    if (!data) return
    let data_temp = (Array.isArray(data)) ? data : [data]
    this._data1.next( data_temp );
  }

  get data(): any { return this._data1.getValue(); }


  objectKeys = Object.keys;

  constructor(
    private br: BridgeService,
    private sidebar: SidebarGeneralListComponent,
  ) { }

  ngOnInit() {
    //console.log(this.params, this._data1)
    this.br.miniDestroy
      .pipe(
        tap(data => {
          // console.log(data)
          this.destroy$.next()
        })
      )
      .subscribe()
  }

  ngOnDestroy(){
    console.log('destroy');
    this.destroy$.next();
    this.destroy$.complete();
  }

  drop(event: CdkDragDrop<string[]>) {
    // console.log(event)
    // console.log(this._data2)
    // console.log(this._data1)
    moveItemInArray(this._data1.getValue(), event.previousIndex, event.currentIndex);
  }

  deleteItem(item: number){
    // console.log(item)
    this._data1.getValue().splice(item,1)
    this.returnData.emit(this._data1.getValue())
  }

  showSidebar(event){
    this.br.completeTrigger(true)
    if (event.target.className == 'del_item') return;
    //console.log(this._data1, this._data2)
    this.br.changeData(this._data1.getValue())
    this.br.changeParametes(this.params)
    this.br.currentData
      .pipe(
        takeUntil(this.destroy$),
        tap(data => {
          this.returnData.emit(data)
        })
      )

      .subscribe(data => {
      // console.log(data)
      this._data1.next(data)
    })
    this.sidebar.show({})
  }

}
