import { Component, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

import { SideBarFormService } from './sidebar-form.service';
import { SidebarFormBridge } from './sidebar-form.bridge';

import { Address } from '../../_forms/obj-addresses-form/obj-address.model'
import { CMD } from '../../_forms/obj-commands-form/obj-commands.model'
import { AddressesService } from '../../../../../pages/tacacs/objects/addresses/addresses.service'
import { CMDService } from '../../../../../pages/tacacs/objects/command-sets/cmd.service'

export interface SidebarFormComponent {
  data: any;
}

@Component({
  selector: 'kt-sidebar-new-element',
  templateUrl: './sidebar-new-element.component.html',
  styleUrls: ['./sidebar-new-element.component.scss']
})
export class SidebarNewElementComponent implements OnInit {

  private service: string = ''
  private dataCheck: string = ''

  private offcanvas: any
  title = ''
  private params = ''
  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(false)

  formParameters = {
    action: 'add',
    data: {},
    component: 'address',
    type: ''
  }

  @ViewChild('sidebar_form', {
    read: ViewContainerRef
  }) viewContainerRef: ViewContainerRef

  constructor(
    private elbridge: SidebarFormBridge,
    private sFormService: SideBarFormService,
    private addrService: AddressesService,
    private cmdService: CMDService
  ) {
  }

  ngOnInit() {
    this.elbridge.parameters.subscribe( data => {
      console.log(data)
      // console.log(this.viewContainerRef)
      // this.viewContainerRef.remove()
      this.viewContainerRef.clear();
      if (data) this.loadComponent(data)
    })
    this.elbridge.openSidebar.subscribe( data => {
      if (data) {

        this.openSidebar()
      }
      // console.log(data)
    })
  }

  openSidebar(){
    let o = {}
    o['overlay'] = true
    o['baseClass'] = 'kt-quick-panel'
    o['closeBy'] = 'kt_new_item_panel_close_btn'
    o['toggleBy'] = ''
    this.offcanvas = new KTOffcanvas( document.querySelector('#sidebar-new-item'), o );
    this.offcanvas.show()
  }

  closeSidebar(){
    this.validation.next({})
    this.offcanvas.hide()
  }

  loadComponent(formType) {
    // console.log(this.sFormService)
    // console.log(this.viewContainerRef)
    this.prepareData(formType)
    this.sFormService.setRootViewContainerRef(this.viewContainerRef)
    this.sFormService.addDynamicComponent({validation: this.validation, options: this.formParameters, loading: this.loadingForm})
  }

  prepareData(formType){
    // console.log(formType)
    switch (formType.addNew) {
      case 'address':
        this.service = 'addrService'
        this.dataCheck = 'address'
        this.title = 'Add New Address';
        let address = new Address
        address.empty()
        this.formParameters.component = formType.addNew
        this.formParameters.data =  (address as {})
        // this.formParameters.data.empty()
        break;
      case 'cmd':
        this.service = 'cmdService'
        this.dataCheck = 'cmd'
        this.title = 'Add New CMD Set';
        let cmd = new CMD
        cmd.empty()
        this.formParameters.data =  (cmd as {})
        this.formParameters.component = formType.addNew
        this.formParameters.type = formType.type
        break;
      default:
        this.title = 'Unknown';
        this.formParameters = {
          action: 'add',
          data: {},
          component: 'address',
          type: ''
        }
    }
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  add(){
    // console.log(this.formParameters.data)
    let data = this.makeClone(this.formParameters.data)
    this.loadingForm.next(true)
    if (this.service == 'cmdService'){
      data.junos = (data.junos.length) ? data.junos.join(',') : '';
    }
    this[this.service].add(data).subscribe(data => {
      // console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status) {
        this.loadingForm.next(false)
        return
      }
      if (data[this.dataCheck]) {
        this.elbridge.added()
        this.offcanvas.hide()
      }
      this.loadingForm.next(false)
    })

  }

}
