import { Component, OnInit, OnDestroy, Input, ViewChildren, QueryList, ViewEncapsulation } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { finalize, takeUntil, tap, map, skip } from 'rxjs/operators';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Router
import { Router, ActivatedRoute } from '@angular/router';

import { MainTable } from './main-table.model'

import { NgbdSortableHeader, SortEvent } from './sortable.directive'
import { MainTableService } from './main-table.service'
import { PreviewService } from './preview.service'

interface PreviewItem {
  collapsed: boolean;
  message: Observable<any>;
}

@Component({
  selector: 'kt-main-table',
  templateUrl: './main-table.component.html',
  styleUrls: ['./main-table.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class MainTableComponent implements OnInit, OnDestroy {

  @Input() options: MainTable;
  @Input() reload: BehaviorSubject<boolean> = new BehaviorSubject(false);

  $destroyer = new BehaviorSubject<boolean>(false);
  objectKeys = Object.keys;
  tabledata$: Observable<any>;
  total$: Observable<number>;

  initialSort = {
    column: '',
    direction: ''
  }

  columnShown$ = new BehaviorSubject<number>(0);

  selected = {}
  collapsed = {}

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;

  constructor(
    private td: MainTableService,
    private ps: PreviewService,
    private toastr:ToastrService,
    private router:Router,
    private route: ActivatedRoute
  ) {
      //console.log(123123)
   }

  ngOnInit() {
    this.td.reset();
    this.td.apiurl = this.options.table.mainUrl
    // console.log(this.options.table.extra)
    if (this.options.table.extra)
      this.td.extra = this.options.table.extra
    if (this.options.table.sort){
      this.td.sortColumn = this.options.table.sort.column;
      this.td.sortDirection = this.options.table.sort.direction;
      // this.initialSort.column = this.options.table.sort.column;
      // this.initialSort.direction = this.options.table.sort.direction;
    } else {
      this.td.sortColumn = '';
      this.td.sortDirection = '';
    }
    this.tabledata$ = this.td.tabledata$;
    if (this.options.table.pagination && this.options.table.pagination.perpageItems){
      this.td.pageSize = this.options.table.pagination.perpageItems[0]
    }
    //console.log(this.td.apiurl)
    this.columnShown$.next(this.shownCoumnCount())

    this.reload.subscribe(data => { //pipe(takeUntil(this.$destroyer))
      console.log('table reload')
      if (data) this.tabledata$ = this.td.tabledata$;
    })
  }

  tableInit(){

  }

  ngOnDestroy() {
    //console.log('destroy');
    // this.td.extra = {}
    this.$destroyer.next(true)
    this.$destroyer.unsubscribe()
  }

  shownCoumnCount(){
    let conter = 0
    for (let column in this.options.table.columns) {
      if (this.options.table.columns[column].show) conter++
    }
    if (this.options.table.preview) conter++
    if (this.options.table.delBtn || this.options.table.editBtn) conter++
    return conter;
  }

  showColumn(column:string, switcher:string = ''){
    if (!this.options.table.columns[column]) {
      return false;
    }
    //console.log(this.options.table.columns[column].show)
    if (switcher) {
      this.options.table.columns[column].show = !this.options.table.columns[column].show
      this.columnShown$.next(this.shownCoumnCount())
      return true;
    }
    return this.options.table.columns[column].show
  }

  onSort({column, direction}: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    this.td.sortColumn = column;
    this.td.sortDirection = direction;
  }

  htmlPattern(data_cel, column, index) {
    if ( this.options.table.columns[column].hasOwnProperty('htmlPattern') ) {
      return this.options.table.columns[column].htmlPattern(data_cel, column, index, this.tabledata$)
    }
    return data_cel
  }

  stopSelection(evt: MouseEvent) {
    evt.stopPropagation()
  }

  selectItem(id, event){
    //console.log(id, event)
    if ( !this.options.table.selectable ) return
    if (event.target.className.split(' ').includes('button-preview')) return false;
    if (event.ctrlKey) {
      if (this.selected.hasOwnProperty(id)) {
        delete this.selected[id];
        return;
      }
      this.selected[id] = true;
      return;
    }

    if (this.selected.hasOwnProperty(id)) {
      delete this.selected[id];
      return;
    }

    this.selected = {}
    this.selected[id] = true;
    return;
  }

  showPreview(id, name, username){
    if (!this.collapsed[id]){
      this.collapsed[id] = {
        collapsed: true,
        message: new BehaviorSubject('')
      }
      this.ps.get(this.options.table.preview.target, id, name, username)
      .subscribe(data => {
        // console.log(data)
        // if (typeof data[0] == 'object') data.shift()
        //console.log(data.join('\n'))
        this.collapsed[id].message.next(data.join('\n'))
      })
      return true
    }
    else {
      delete this.collapsed[id]
      return true
    }
  }

  deleteItem(id:number, name:string, event){
    if ( !confirm('Do you want to delete '+name+ ' ?')) return false;
    event.target.closest('tr').remove()
    this.td.delItem(id, name, this.options.table.delBtn)
      .subscribe(data => {
        //console.log(data)
        if (data == 1){
          this.toastr.success('Successfully deleted');
          this.tabledata$ = this.td.tabledata$;
          return
        }
        this.toastr.error('Unknown error');
        this.tabledata$ = this.td.tabledata$;
        return
      })


  }

  columnClick(data_cel, column, index, e){
    if ( this.options.table.columns[column].hasOwnProperty('onClick') ) {
      if (this.options.table.columns[column].onClick(data_cel, column, index, this.tabledata$, e))
        this.tabledata$ = this.td.tabledata$;
    }
  }

  edit(id){
    // console.log(id)
    if ( !this.options.table.editable ) return
    this.router.navigate(['./edit/'+id], { relativeTo: this.route });
  }

}
