import {
  ComponentFactoryResolver,
  Injectable,
} from '@angular/core'

import { ObjAddressesFormComponent } from '../../_forms/form-dynamic/obj-addresses-form-dynamic/obj-addresses-form.component'
import { ObjCommandsFormComponent } from '../../_forms/form-dynamic/obj-commands-form-dynamic/obj-commands-form.component'
// import { SidebarForm }               from './sidebar-form.model';

@Injectable({
  providedIn: 'root'
})

export class SideBarFormService {
  rootViewContainer: any
  constructor( private factoryResolver: ComponentFactoryResolver) {
  }
  setRootViewContainerRef(viewContainerRef) {
    this.rootViewContainer = viewContainerRef
  }
  addDynamicComponent(data:any = {}) {

    console.log(data)
    let factory;
    switch (data.options.component) {
      case 'address':
        console.log('address')
        factory = this.factoryResolver.resolveComponentFactory(ObjAddressesFormComponent)
        break;
      case 'cmd':
        console.log('cmd')
        factory = this.factoryResolver.resolveComponentFactory(ObjCommandsFormComponent)
        break;
      default:
        console.log('default')
        factory = this.factoryResolver.resolveComponentFactory(ObjAddressesFormComponent)
    }

    let component = factory
      .create(this.rootViewContainer.parentInjector)
    Object.assign(component.instance, data)
    console.log(component)
    this.rootViewContainer.insert(component.hostView)
  }

}
