import { Component, OnInit } from '@angular/core';
import { AuthNoticeService } from '../../../../core/auth';

//Service
import { ChangeService } from './change.service'

@Component({
  selector: 'kt-tacchange',
  templateUrl: './tacchange.component.html'
})
export class TacchangeComponent implements OnInit {

  private username:string
  private oldpass:string
  private password:string
  private password2:string

  constructor(
    private authNoticeService: AuthNoticeService,
    private service: ChangeService
  ) {
    // this.unsubscribe = new Subject();
  }

  ngOnInit() {
  }

  change(){
    // console.log(this.password, this.password2)
    this.authNoticeService.setNotice(null, 'danger');
    if ( !this.password || !this.password2 || !this.oldpass ){
      this.authNoticeService.setNotice('Password can not be empty', 'danger');
      return
    }
    if (this.password != this.password2){
      this.authNoticeService.setNotice('Passwords do not match', 'danger');
      return
    }

    this.service.change({
      username: this.username,
      object: 'login',
      password: this.oldpass,
      new_password: this.password,
      new_password_repeat: this.password2,
    }).subscribe(data => {
      console.log(data)
      if ( data.error.status ){
        this.authNoticeService.setNotice(data.error.message, 'danger');
        return
      }
      this.password = ''
      this.password2 = ''
      this.oldpass = ''
      this.username = ''
      this.authNoticeService.setNotice('Password changed', 'success');
    })
  }

}
