import { Injectable, Injector } from '@angular/core';

import {
  HttpEvent, HttpInterceptor, HttpHandler, HttpRequest, HttpErrorResponse
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
//Toastr
import { ToastrService } from 'ngx-toastr';

//Service
import { SuperDataService } from '../../tacgui/super-data.service'

import { environment } from '../../../../environments/environment';

import { AuthService } from '../_services/auth.service';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {
  private authService: AuthService;
  constructor(private injector: Injector, private super_: SuperDataService) {}
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    this.authService = this.injector.get(AuthService);
    const token: string = this.authService.getToken();
    request = request.clone({
      setHeaders: {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      }
    });
    return next.handle(request);
  }
}

@Injectable()
export class ErrorInterceptor implements HttpInterceptor {
  constructor(
    private router: Router,
    private toastr:ToastrService,
    private super_: SuperDataService
  ) {}
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(request)
      .pipe(
        map( data => {
          let resp = data as { body: { changeConfiguration: number } }
          if (resp.body && resp.body.changeConfiguration != undefined){
            this.super_.cfgChange = !!resp.body.changeConfiguration;
          }
            // console.log(resp.body.changeConfiguration)
          return data
        }),
        catchError( (response: any) => {
          if (response instanceof HttpErrorResponse && response.status === 401) {
            console.log(response);
            localStorage.removeItem(environment.authTokenKey);
            localStorage.removeItem(environment.user);
            this.router.navigateByUrl('/auth');
          }
          if (response instanceof HttpErrorResponse && response.status === 403) {
            this.toastr.warning('Action Forbidden!')
          }
          return throwError(response);
        })//catchError
      );//pipe
  }
}
