import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs'
// import { delay,first } from 'rxjs/operators'

@Injectable({
  providedIn: 'root'
})
export class SidebarFormBridge {

  private open = new BehaviorSubject<boolean>(false);
  private add = new BehaviorSubject<boolean>(false);
  private params = new BehaviorSubject<any>('');

  openSidebar = this.open.asObservable();
  parameters = this.params.asObservable();
  addItem = this.add.asObservable();

  constructor() { }

  changeOpen(data: any) {
    this.open.next(data)
  }

  changeParametes(data: any) {
    this.params.next(data)
  }

  added(trigger: any = true) {
    this.add.next(trigger)
  }

}
