import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable, BehaviorSubject, of, Subject } from 'rxjs';
import { map, tap, delay, switchMap, debounceTime } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class UpgradeService {

  private _search$ = new Subject<void>();
  private _loading$ = new BehaviorSubject<boolean>(true);
  private _complete$ = new BehaviorSubject<boolean>(false);

  private _message$ = new BehaviorSubject<string>('');
  private _progress$ = new BehaviorSubject<any>([]);
  private _check$ = new BehaviorSubject<number>(0);
  private counter = [];

  currentStatus = this._complete$.asObservable();

  constructor(private http: HttpClient) {
    this._search$.pipe(
      tap(() => this._loading$.next(true)),
      // debounceTime(400),
      switchMap(() => this._search()),
      delay(400),
      //map(data => data.results),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      console.log(result)
      if (this._check$.getValue() !== 0){
        if (result.messages && result.messages.length){
          this._search$.next();
        } else {
          this._complete$.next(true)
        }
      } else {
        this._complete$.next(true)
      }

      // this._listdata$.next(result);
      //this._total$.next(result.total);
    });

    this._search$.next();
  }

  get loading$() { return this._loading$.asObservable(); }
  get message$() { return this._message$.asObservable(); }
  get progress$() { return this._progress$.asObservable(); }
  // get check$() { return this._check$.asObservable().getValue(); }
  //
  set check$(trigger: number) { this._check$.next(trigger); }

  public start$ (){

    //this._search$().next()
  }

  public _search(): Observable<any> {
    this._loading$.next(true)
    const params = new HttpParams()
    .set('update', this._check$.getValue().toString())
    // 1. sort
    return this.http.get<any>('/api/apicheck/database/', {params})
          .pipe(
            tap( data => {
              console.log(data)
              this._loading$.next(false)
              if (this._check$.getValue() !== 0 ) this.counter.push(Math.round(Math.random()))
              this._progress$.next(this.counter)
              if (data.messages) this._message$.next(data.messages)
            })
          );

  }

}
