// Angular
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// RxJS
import { Observable, Subject, BehaviorSubject } from 'rxjs';
import { finalize, takeUntil, tap, map, first } from 'rxjs/operators';
// Translate
import { TranslateService } from '@ngx-translate/core';
// Store
import { Store } from '@ngrx/store';
import { AppState } from '../../../../core/reducers';
// Auth
import { AuthNoticeService, AuthService, Login } from '../../../../core/auth';
//Service
import { TestService } from './test.service'

/**
 * ! Just example => Should be removed in development
 */
const DEMO_PARAMS = {
	USERNAME: '',
	PASSWORD: ''
};

@Component({
	selector: 'kt-login',
	templateUrl: './login.component.html',
	encapsulation: ViewEncapsulation.None
})
export class LoginComponent implements OnInit, OnDestroy {
	// Public params
	loginForm: FormGroup;
	loading = false;
	isLoggedIn$: Observable<boolean>;
	errors: any = [];
	tacacs_change = new BehaviorSubject<number>(0)

	mainUrl = window.location.href.replace(/(http:\/\/|https:\/\/)/i,'').replace(/(\/.*$)/i, '')


	private unsubscribe: Subject<any>; // Read more: => https://brianflove.com/2016/12/11/anguar-2-unsubscribe-observables/

	/**
	 * Component constructor
	 *
	 * @param router: Router
	 * @param auth: AuthService
	 * @param authNoticeService: AuthNoticeService
	 * @param translate: TranslateService
	 * @param store: Store<AppState>
	 * @param fb: FormBuilder
	 * @param cdr
	 */
	constructor(
		private router: Router,
		private auth: AuthService,
		private authNoticeService: AuthNoticeService,
		private translate: TranslateService,
		private store: Store<AppState>,
		private fb: FormBuilder,
		private cdr: ChangeDetectorRef,
		private test: TestService
	) {
		this.unsubscribe = new Subject();
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit(): void {
		this.test.run().subscribe(null, err => {
			console.log(err)
			this.tacacs_change.next(err.error.tacacs)
		})
		console.log(this.auth.getToken())
		if (this.auth.getToken()){
			this.auth.getUserByToken()
				.pipe(
					tap(data => {
						console.log(data)
						if (data.user && data.token) {
							this.store.dispatch(new Login({
								authToken: data.token,
								user: data.user
							}));
							this.router.navigateByUrl('/'); // Main page
						}
					}),
					first(),
					finalize(() => {
						this.loading = false;
						this.cdr.detectChanges();
					})
				) //pipe
				.subscribe(null,
				error => {
					this.initLoginForm();
				})
		} else {
			this.initLoginForm();
		}
	}

	/**
	 * On destroy
	 */
	ngOnDestroy(): void {
		this.authNoticeService.setNotice(null);
		this.unsubscribe.next();
		this.unsubscribe.complete();
		this.loading = false;
	}

	/**
	 * Form initalization
	 * Default params, validators
	 */
	initLoginForm() {
		// demo message to show
		if (!this.authNoticeService.onNoticeChanged$.getValue()) {
			let demo = (this.mainUrl == 'newdemo.tacacsgui.com:4443' || (this.mainUrl == 'newdemo.tacacsgui.com:8008'))
			console.log(this.mainUrl);
			DEMO_PARAMS.USERNAME = (demo) ? 'demo' : DEMO_PARAMS.USERNAME;
			DEMO_PARAMS.PASSWORD = (demo) ? 'demo' : DEMO_PARAMS.PASSWORD;
			const initialNotice = `Use account
			<strong>${DEMO_PARAMS.USERNAME}</strong> and password
			<strong>${DEMO_PARAMS.PASSWORD}</strong> to continue.`;
			this.authNoticeService.setNotice(null, 'info');
			if ( demo ) this.authNoticeService.setNotice(initialNotice, 'info');
		}

		this.loginForm = this.fb.group({
			username: [DEMO_PARAMS.USERNAME, Validators.compose([
				Validators.required,
				Validators.minLength(3),
				Validators.maxLength(320) // https://stackoverflow.com/questions/386294/what-is-the-maximum-length-of-a-valid-email-address
			])
			],
			password: [DEMO_PARAMS.PASSWORD, Validators.compose([
				Validators.required,
				Validators.minLength(3),
				Validators.maxLength(100)
			])
			]
		});
	}

	/**
	 * Form Submit
	 */
	submit() {
		const controls = this.loginForm.controls;
		/** check form */
		if (this.loginForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);
			return;
		}

		this.loading = true;

		const authData = {
			username: controls['username'].value,
			password: controls['password'].value
		};
		this.auth
			.login(authData.username, authData.password)
			.pipe(
				tap(data => {
					console.log(data)
					if (data.user && data.token) {
						console.log(data)
						let user = data.user as any
						if ( user.changePasswd ) {
							console.log('change password');
							console.log('/auth/change-password/'+ authData.username)
							this.router.navigateByUrl('/auth/change-password/'+ authData.username);
						} else {
							this.store.dispatch(new Login({
								authToken: data.token,
								user: data.user
							}));
							this.router.navigateByUrl('/'); // Main page
						}
					} else {
						this.authNoticeService.setNotice(this.translate.instant('AUTH.VALIDATION.INVALID_LOGIN'), 'danger');
					}
				}),
				first(),
				finalize(() => {
					this.loading = false;
					this.cdr.detectChanges();
				})
			) //pipe
			.subscribe( data => {
				this.authNoticeService.setNotice(null, 'danger');
			},
			err => {
				if (err.status === 401 ) {
					this.authNoticeService.setNotice('Incorrect login or password', 'danger');
				} else {
					this.authNoticeService.setNotice('Server error! '+ err.status, 'danger');
				}
			}
			);
	}

	/**
	 * Checking control validation
	 *
	 * @param controlName: string => Equals to formControlName
	 * @param validationType: string => Equals to valitors name
	 */
	isControlHasError(controlName: string, validationType: string): boolean {
		const control = this.loginForm.controls[controlName];
		if (!control) {
			return false;
		}

		const result = control.hasError(validationType) && (control.dirty || control.touched);
		return result;
	}
}
