// Angular
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
// RxJS
import { finalize, takeUntil, tap } from 'rxjs/operators';
import { Subject } from 'rxjs';
// Translate
import { TranslateService } from '@ngx-translate/core';
// Store
import { Store } from '@ngrx/store';
import { AppState } from '../../../../core/reducers';
// Auth
import { AuthNoticeService, AuthService, Login } from '../../../../core/auth';
//Service
import { ChangeService } from './change.service'

@Component({
	selector: 'kt-forgot-password',
	templateUrl: './forgot-password.component.html',
	encapsulation: ViewEncapsulation.None
})
export class ForgotPasswordComponent implements OnInit, OnDestroy {
	// Public params
	forgotPasswordForm: FormGroup;
	private password: string;
	private password2: string;
	loading = false;
	errors: any = [];

	private unsubscribe: Subject<any>; // Read more: => https://brianflove.com/2016/12/11/anguar-2-unsubscribe-observables/

	/**
	 * Component constructor
	 *
	 * @param authService
	 * @param authNoticeService
	 * @param translate
	 * @param router
	 * @param fb
	 * @param cdr
	 */
	constructor(
		private authService: AuthService,
		public authNoticeService: AuthNoticeService,
		private translate: TranslateService,
		private router: Router,
		private store: Store<AppState>,
		// private fb: FormBuilder,
		private cdr: ChangeDetectorRef,
		private service: ChangeService
	) {
		this.unsubscribe = new Subject();
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
	}

	/**
	 * On destroy
	 */
	ngOnDestroy(): void {
		this.unsubscribe.next();
		this.unsubscribe.complete();
		this.loading = false;
	}

	/**
	 * Form Submit
	 */
	submit() {
		console.log(this.password, this.password2)
		if (!this.password || !this.password2){
			this.authNoticeService.setNotice('Password can not be empty', 'danger');
			return
		}
		if (this.password != this.password2){
			this.authNoticeService.setNotice('Passwords do not match', 'danger');
			return
		}
		this.loading = true;

		this.service.change({
			change_passwd: this.password,
			change_passwd_repeat: this.password2,
		}).subscribe(data => {
			console.log(data)
			if (data.error.status){
				let message = ''
				for (let key in data.error.validation) {
	        if (data.error.validation[key]) {
	          //console.log(data.error.validation[key])
	          for (let i = 0; i < data.error.validation[key].length; i++) {
	             message += (message !== '') ? '<br>' + data.error.validation[key][i] : data.error.validation[key][i];
	          }
	        }
	      }
				this.authNoticeService.setNotice(message, 'danger');
				this.loading = false;
				return
			} else {
				// this.store.dispatch(new Login({
				// 	authToken: data.token,
				// 	user: data.user
				// }));
				this.router.navigateByUrl('/auth/login'); // Main page
			}
		})

	}

}
