import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { skip, tap, first } from 'rxjs/operators';
import { BridgeService } from '../../_fields/field-general-list/bridge.service'
import { SideBarGeneralListService } from './sidebar-general-list.service'
import { SidebarFormBridge } from '../sidebar-new-element/sidebar-form.bridge'

@Component({
  selector: 'kt-sidebar-general-list',
  templateUrl: './sidebar-general-list.component.html',
  styleUrls: ['./sidebar-general-list.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class SidebarGeneralListComponent implements OnInit {

  data = []

  objectKeys = Object.keys;

  private destroy$ = new Subject();

  listdata$: Observable<any>;
  params = {
    title_sidebar: 'Udefined',
    search: false,
    apiurl: '',
    multiple: false,
    addNew: ''
  }

  constructor(
    private br: BridgeService,
    private list: SideBarGeneralListService,
    private elbridge: SidebarFormBridge
  ) {
    //console.log(123123)
  }

  ngOnInit() {
    this.br.parameters.subscribe(data => {
      this.params = data
    })
    this.br.currentData.subscribe(data => {
      // console.log(data)
      this.data = data
    })
    this.br.miniDestroy
      .pipe(
        tap(data => {
          // console.log(data)
          this.data = []
        })
      )
      .subscribe()
  }

  ngOnDestroy(){
    // console.log('destroy');
    this.destroy$.next();
    this.destroy$.complete();
  }

  show(o){
    this.list.searchTerm = ''
    this.br.parameters.pipe(first() ).subscribe(data => {
      this.params = data
    })
    this.br.currentData.pipe(first() ).subscribe(data => {
      // console.log(data)
      this.data = data
    })
    //console.log(this.br.parameters)
    this.list.apiurl = this.params.apiurl
    this.listdata$ = this.list.listdata$;
    // console.log(this.listdata$)
    o = o || {}
    o.overlay = (o.overlay === undefined) ? true : o.overlay
    o.baseClass = (o.baseClass === undefined) ? 'kt-quick-panel' : o.baseClass
    o.closeBy = (o.closeBy === undefined) ? 'kt_panel_close_btn' : o.closeBy
    o.toggleBy = (o.toggleBy === undefined) ? '' : o.toggleBy
    let offcanvas = new KTOffcanvas( document.querySelector('#general-sidebar-list'), o );
    offcanvas.show()
    // console.log(this.data)
  }

  addNewElement(){
    this.elbridge.addItem.pipe(skip(1), first()).subscribe(data =>{
      // console.log(data)
      this.list.reload$();
    })
    this.elbridge.changeParametes(this.params)
    this.elbridge.changeOpen(1)
  }

  checkSelected(id){
    return !!(this.data.find(function(item){
      return item.id == id;
    }));
  }

  addElement(el){
    console.log(el)
    console.log(this.data)
    let someSearch = this.data.findIndex(function(item){
      return JSON.stringify(item) == JSON.stringify(el);
    });
    if (someSearch > -1) {
      console.log(someSearch)
      this.data.splice(someSearch,1)
      this.br.changeData(this.data)
      return
    }
    if (this.params.multiple) this.data.push(el)
    else this.data = [el];
    this.br.changeData(this.data)
  }

}
