import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs'
import { delay,first } from 'rxjs/operators'

@Injectable({
  providedIn: 'root'
})
export class SuperDataService {

  private _cfgChange = new BehaviorSubject<boolean>(false);
  private _token = new BehaviorSubject<any>( '' );
  private _user = new BehaviorSubject<any>( { id:0, username: '' } );

  constructor() { }

  get cfgChange() { return this._cfgChange.asObservable(); }
  set cfgChange( trigger: any ) { this._cfgChange.next(trigger); }

  get token() { return this._token.getValue(); }
  set token( token: string ) { this._token.next(token); }

  get user() { return this._user.getValue(); }
  set user( user: string ) { this._user.next(user); }

}
