export class CMD {
  cmd: string
  cmd_attr: any
  cmd_permit_end: number
  junos: any
  manual: string
  message_deny: string
  message_permit: string
  id: number;
  name: string;
  type: number;
  created_at:string;
  updated_at: string;

    empty(): void {
      this.cmd = ''
      this.cmd_attr = [];
      this.junos = [];
      this.cmd_permit_end = 0
      this.manual = ''
      this.message_deny = ''
      this.message_permit = ''
      this.created_at=undefined;
      this.id=undefined;
      this.name=undefined;
      this.type=0;
      this.updated_at=undefined;
    }
}
