import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs'
import { delay,first } from 'rxjs/operators'

@Injectable({
  providedIn: 'root'
})
export class BridgeService {

  private data = new BehaviorSubject<any>({});
  private params = new BehaviorSubject<any>({});
  private stop = new BehaviorSubject<any>(true);

  currentData = this.data.asObservable();
  parameters = this.params.asObservable();
  miniDestroy = this.stop.asObservable();

  constructor() { }

  changeData(data: any) {
    this.data.next(data)
  }

  changeParametes(data: any) {
    this.params.next(data)
  }

  completeTrigger(data){
    this.stop.next(data)
  }

}
