import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject, of, Subject } from 'rxjs';
import { map, tap, delay, switchMap, debounceTime, takeUntil, first } from 'rxjs/operators';
import { SortDirection } from './sortable.directive'

interface SearchResult {
  tabledata: any;
  total: number;
}

interface State {
  page: number;
  apiurl: string;
  pageSize: number;
  searchTerm: string;
  sortColumn: string;
  sortDirection: SortDirection;
  extra: any;
}

@Injectable({
  providedIn: 'root'
})

export class MainTableService {

  private _loading$ = new BehaviorSubject<boolean>(true);
  private _errors$ = new BehaviorSubject<any>([]);
  private _extra$ = new BehaviorSubject<any>({});
  public _search$ = new Subject<void>();
  public destroy$ = new Subject<void>();
  private _tabledata$ = new BehaviorSubject<any>([]);
  private _total$ = new BehaviorSubject<number>(0);

  private _state: State = {
    page: 1,
    apiurl: '',
    pageSize: 5,
    searchTerm: '',
    sortColumn: '',
    sortDirection: '',
    extra: {}
  };

  constructor(private http: HttpClient) {
    this._search$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._search()),
      delay(200),
      tap(() => this._loading$.next(false)),
      takeUntil(this.destroy$)
    ).subscribe(result => {
      //console.log(result)
      this._tabledata$.next(result);
      //this._total$.next(result.total);
    });

    //this._search$.next();
  }

  get apiurl() { return this._state.apiurl; }
  get tabledata$() { this._search$.next(); return this._tabledata$.asObservable(); }
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get errors$() { return this._errors$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get searchTerm() { return this._state.searchTerm; }
  get sortColumn() { return this._state.sortColumn; }
  get searsortDirectionchTerm() { return this._state.sortDirection; }

  set extra(extra: any) { this._extra$.next(extra); }
  set apiurl(apiurl: string) { this._set({apiurl}); }
  set page(page: number) { this._set({page}); }
  set pageSize(pageSize: number) { this._set({pageSize}); }
  set searchTerm(searchTerm: string) { this._set({searchTerm}); }
  set sortColumn(sortColumn: string) { this._set({sortColumn}); }
  set sortDirection(sortDirection: SortDirection) { this._set({sortDirection}); }

  reset() {
    this._tabledata$.next([]);
    this._extra$.next({});
  }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    this._search$.next();
  }

  private _search(): Observable<SearchResult> {
    const tableSettings = {
      page: this._state.page,
      pageSize: this._state.pageSize,
      searchTerm: this._state.searchTerm,
      sortColumn: this._state.sortColumn,
      sortDirection: this._state.sortDirection,
      extra: this._extra$.getValue(),
    }
    //const {sortColumn, sortDirection, pageSize, page, searchTerm} = this._state;
    // 1. sort
    return this.http.post<any>('api'+this._state.apiurl, tableSettings)
      .pipe(
        map(data => {
          if (data.total) this._total$.next(data.total)
          this._errors$.next([])
          if (data.database && data.database.errors) this._errors$.next(data.database.errors)
          return data.data
        })
      );

    // 2. filter
    // tabledata = tabledata.filter(country => matches(country, searchTerm, this.pipe));
    // const total = tabledata.length;

    // 3. paginate
    // tabledata = tabledata.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);
    // return of({tabledata, total});
  }

  delItem(id:number, name:string, url:string){
    return this.http.post<any>('api' + url, { id, name })
      .pipe(
        debounceTime(600),
        first(),
        map( (data) => {
          console.log(data)
          return data.result
        } )
      )
  }
}
