import { Component, OnInit, ViewEncapsulation } from '@angular/core';
//Router
import { Router } from '@angular/router';
// RXJS
import { BehaviorSubject,Subject, timer } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { UpgradeService } from './upgrade.service';

@Component({
  selector: 'kt-upgrade',
  templateUrl: './upgrade.component.html',
  styleUrls: ['./upgrade.component.scss'],
})
export class UpgradeComponent implements OnInit {

  private destroy$ = new Subject();
  private complete_start = new BehaviorSubject<boolean>(false);
  private timer = new BehaviorSubject<number>(7);
  desc = 'Looks like we have to upgrade something...'
  ticks = 7;

  constructor(
    private router: Router,
    private us: UpgradeService
  ) {
    us.check$ = 1
    us.currentStatus.subscribe(data => {
      console.log(data)
      if (data) this.complete()
    })

  }

  ngOnInit() {
  }

  complete(){
    this.complete_start.next(true);
    const source = timer(1000, 1000);
    source.pipe(takeUntil(this.destroy$)).subscribe(
      val => {
        console.log(val);
        if ( this.timer.getValue() === 0 ){
          this.router.navigateByUrl('/'); // Main page
        }
        else {
          this.timer.next(this.timer.getValue()-1);
        }

      }
    );
  }

  ngOnDestroy(): void {
    this.destroy$.next()
    this.destroy$.complete()
  }

}
