import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map, first } from 'rxjs/operators';

const API_URL = 'api/obj/cmd';

@Injectable({
  providedIn: 'root'
})
export class CMDService {

  objectKeys = Object.keys;

  constructor(private http: HttpClient) {}

  add(cmd:{}) {
    //let message = ''
    return this.http.post<any>(API_URL + '/add/', cmd)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  get(id:number) {
    const params = new HttpParams()
    .set('id', id.toString());
    //let message = ''
    return this.http.get<any>(API_URL + '/edit/', { params })
      .pipe(
        first(),
        map( (data) => {
          return data.cmd
        } )
      )
  }

  save(cmd:{}) {
    //let message = ''
    return this.http.post<any>(API_URL + '/edit/', cmd)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }
}
