export class ApiUser {
  id: number;
  username: string;
  changePasswd?: number;
  bad_authentication_notice?: number
  bad_authorization_notice?: number
  cmd_type?: number
  created_at?: string
  email?: string
  firstname?: string
  group?: any
  position?: string
  surname?: string
  updated_at?: string

    empty(): void {
      this.id=undefined;
      this.username='';
      this.group=[];
      this.changePasswd = 1;
    }
}
